// Brand
$color-canopy: rgb(69, 190, 147) !default;
$color-canopy-light: lighten($color-canopy, 15%) !default;
$color-canopy-dark: darken($color-canopy, 15%) !default;
$color-secondary: rgb(115, 29, 216) !default;
$color-secondary-light: lighten($color-secondary, 15%);
$color-secondary-dark: darken($color-secondary, 15%);
$color-tertiary: rgb(255, 91, 50) !default;
$color-tertiary-light: lighten($color-tertiary, 15%);
$color-tertiary-dark: darken($color-tertiary, 15%);

$brand-colors: (
  'canopy-light': $color-canopy-light,
  'canopy': $color-canopy,
  'canopy-dark': $color-canopy-dark,
  'secondary-light': $color-secondary-light,
  'secondary': $color-secondary,
  'secondary-dark': $color-secondary-dark,
  'tertiary-light': $color-tertiary-light,
  'tertiary': $color-tertiary,
  'tertiary-dark': $color-tertiary-dark
);
@each $name, $color in $brand-colors {
  .background-#{$name} {
    background: $color;
  }
}

// Components
$link-color: #4d9de0;

// Backgrounds
$background-white: rgb(255, 255, 255) !default;
$background-light: rgb(239, 239, 239) !default;
$background-colors: (
  'white': $background-white,
  'light': $background-light
);

@each $name, $color in $background-colors {
  .background-#{$name} {
    background: $color;
  }
}

// Text
$text-primary: rgb(14, 15, 19) !default;
$text-light: rgba(14, 15, 19, 0.7) !default;
$text-lighter: rgba(14, 15, 19, 0.4) !default;
$text-white: rgb(255, 255, 255) !default;

// Status
$color-success: rgb(0, 211, 74) !default;
$color-danger: rgb(219, 3, 0) !default;
$color-warning: rgb(255, 225, 0) !default;
$color-disabled: rgba(63, 63, 65, 0.4) !default;
$status-colors: (
  'success': $color-success,
  'danger': $color-danger,
  'warning': $color-warning,
  'disabled': $color-disabled
);

@each $name, $color in $status-colors {
  .background-#{$name} {
    background: $color;
  }

  .color-#{$name} {
    color: $color;
  }
}
