#brand-block,
#background-block,
#status-block {
  width: 100%;
  display: flex;
  flex-wrap: wrap;

  .color-block {
    width: 25rem;
    height: 15rem;
    padding: 0 $padding-s $padding-s 0;
    display: flex;
    flex-direction: column;

    &:last-child {
      padding-right: 0;
    }

    .color {
      width: 100%;
      flex: 1 1 100%;
      display: flex;
      justify-content: center;
      align-items: center;

      &:first-child {
        border-top-left-radius: 1rem;
        border-top-right-radius: 1rem;
      }

      &:last-child {
        border-bottom-left-radius: 1rem;
        border-bottom-right-radius: 1rem;
      }
    }
  }
}

#canopy-light {
  background: $color-canopy-light;
}

#canopy {
  background: $color-canopy;
}

#canopy-dark {
  background: $color-canopy-dark;
}

#secondary-light {
  background: $color-secondary-light;
}

#secondary {
  background: $color-secondary;
}

#secondary-dark {
  background: $color-secondary-dark;
}

#tertiary-light {
  background: $color-tertiary-light;
}

#tertiary {
  background: $color-tertiary;
}

#tertiary-dark {
  background: $color-tertiary-dark;
}
